import React from 'react';
import axios from 'axios'
import Images from './Images'

class Search extends React.Component {
    state = {
        keyword: '',
        photos: [],
        loader: null,
        next:''
    }
    InputHandle = (e) => {
        this.setState({
            keyword: e.target.value,

        })
    }

    searchImage = async (e) => {
        e.preventDefault()
        this.setState({ loader: true })
        const res = await axios.get(`https://api.pexels.com/v1/search?query=${this.state.keyword}&per_page=15&page=1`,
            {
                headers: {
                    Authorization: '563492ad6f917000010000017e1dbdb3d63e4046b226bd67847212ba'
                }
            }
        )
        this.setState({ loader: false })
        this.setState({ photos: res.data.photos })
        this.setState({ next: res.data.next_page })
        console.log(this.state.photos)
        console.log(res)
        console.log(this.state.next)

    }


    render() {
        return (
            <div>
                <form onSubmit={this.searchImage}>
                    <div className="form-group">
                        <input type="text" className="form-control"
                            name="keyword" value={this.state.keyword}
                            onChange={this.InputHandle}
                            placeholder="Search Images" />
                    </div>
                    <div className="form-group">
                        <input type="submit"
                            value="search images"
                            className="btn btn-primary btn-block" />

                    </div>

                </form>
                <div className="row">

                    {!this.state.loader ? (
                        this.state.photos.map((img) => (
                            <Images image={img} key={img.id} />
                        ))
                    ) : <h1>Loading</h1>}
                </div>
            </div>

        );
    }
}

export default Search;